import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/v-index.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/:slug',
      name: 'Index',
      component: Index
    },
    {
      path: '/to/:phone',
      name: 'Index',
      component: Index
    }
  ]
})



